@charset "utf-8";
@import "_include/_reset.css";

html {
  font-size: 10px;
}
body {
  font-family: "Roboto", sans-serif;
  font-style: italic;
}
a {
  opacity: 1;
  transition: opacity 0.3s;
  &:hover {
    opacity: 0.8;
  }
}
img {
  display: block;
  max-width: 100%;
}
/* mv */
.mv {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 956px;
  background: url("/assets/images/mv_bg.png") top center no-repeat;
  background-size: cover;
  transition: height 0.3s;
  @media (--sp) {
    height: 600px;
  }
}
.mv__title {
  padding-bottom: 3.2rem;
  color: #fff;
  font-weight: 900;
  font-size: 8rem;
  line-height: 9.4rem;
  transition: font-size 0.3s;
  @media (--sp) {
    font-size: 7rem;
  }
  & p {
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 4.2rem;
  }
}
.mv__subtitle {
  padding-bottom: 4.8rem;
  color: #fff;
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 3.6rem;
  @media (--sp) {
    font-size: 2rem;
  }
}
.mv__sns__list {
  display: flex;
  margin: -8px;
}
.mv__sns__item {
  width: 60px;
  height: 60px;
  padding: 8px;
}
/* mv */

/* seciton */
section {
  &:nth-child(2n) {
    color: #fff;
    background: #000;
  }
}
.contents__wrap {
  max-width: 1200px;
  margin: 0 auto;
  padding: 6.4rem 2vw 5.6rem;
  text-align: center;
  @media (--sp) {
    padding-top: 4.2rem;
    padding-bottom: 3rem;
  }
}

.section__title {
  font-weight: 500;
  font-size: 6.4rem;
  @media (--sp) {
    font-size: 4.4rem;
  }
}
.section__slogn {
  padding-top: 2.4rem;
  font-size: 2rem;
  line-height: 3rem;
  @media (--sp) {
    font-size: 1.8rem;
  }
}
/* seciton */

/* about */
.about__img {
  margin: 0 auto;
  padding-top: 4rem;
}
.about__txt {
  max-width: 412px;
  margin: 0 auto;
  padding-top: 2.4rem;
}
.about__info {
  padding-bottom: 2.4rem;
  text-align: center;
  & li {
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 4.2rem;
    @media (--sp) {
      font-size: 2rem;
    }
  }
}
.about__position {
  position: relative;
  padding-top: 3.2rem;
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 4.2rem;
  border-top: 1px solid #000;
  @media (--sp) {
    font-size: 1.8rem;
  }
  & li {
    display: flex;
    align-items: center;
    justify-content: center;
    &::before {
      width: 22px;
      height: 22px;
      margin-right: 1.6rem;
      background: #891818;
      border-radius: 999rem;
      content: "";
    }
  }
}
/* about */
/* brand */
.brand__txt {
  padding-top: 1.6rem;
  padding-bottom: 0.8rem;
  font-size: 2.4rem;
  line-height: 4.2rem;
}
.brand__img__wrap {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  padding-top: 6.8rem;
  padding-bottom: 10.4rem;
  @media (--sp) {
    padding-bottom: 0;
  }
  & li {
    padding-bottom: 4rem;
    &:first-child {
      margin: 0 auto;
    }
    &:nth-child(n + 2) {
      width: calc((100% / 2) - 12px);
      padding-bottom: 0;
    }
  }
}
/* brand */
/* work */
.work__img__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 5.6rem;
  @media (--sp) {
    padding-top: 3rem;
  }
  & li {
    padding-bottom: 5.6rem;
    @media (--sp) {
      padding-bottom: 1.6rem;
    }
    &:nth-child(-n + 3) {
      width: calc((100% / 3) - 12px);
    }
    &:nth-child(n + 4) {
      width: calc((100% / 2) - 12px);
    }
    &:nth-child(n + 6) {
      width: calc((100% / 3) - 12px);
    }
  }
  & img {
    width: 100%;
  }
  & p {
    padding-top: 1.6rem;
    font-weight: 500;
    font-size: 2rem;
    line-height: 4.2rem;
    @media (--sp) {
      font-size: 1.6rem;
      line-height: 1.5;
    }
  }
}
/* work */
/* footer */
footer {
  padding-bottom: 2.4rem;
  color: #fff;
  background: #000;
}
.footer__txt {
  font-size: 2.4rem;
  line-height: 3.6rem;
  & li {
    padding-top: 1.6rem;
    & a {
      color: #fff;
      text-decoration: none;
    }
  }
}
/* footer */
